import React, { useState } from "react";
import { useDispatch } from "react-redux"; // Import useDispatch
import styles from "./styles.module.scss";
import { updateNumRows } from "../../../../../redux/actions/otherBlocksActions";
import InputBox from "components/UI/common/InputBox/InputBox";
import { numberInputMax } from "helpers/validation";
import FormErrorMessage from "components/UI/common/ErrorMessage";

const BlockTable = ({
  tabelHeader,
  tabelHeader2,
  errors,
  register = () => {},
  otherBlocksList = [],
  handleFloorCount = () => {},
}) => {
  const dispatch = useDispatch(); // Get the dispatch function
  const [userInput, setUserInput] = useState(""); // State to store user input

  // Function to update the number of rows based on user input
  const handleUserInput = (index, event) => {
    const newValue = event.target.value;
    setUserInput(newValue);
    const updatedList = [...otherBlocksList];
    updatedList[index].numberOfFloors = newValue;
    handleFloorCount(event.target.name, newValue);
    // if (!isNaN(newValue)) {
    //   const newNumRows = parseInt(newValue, 10);
    //   dispatch(updateNumRows(newNumRows));
    // }
  };
console.log(otherBlocksList)
  return (
    <div className={styles.ChargesForm}>
      <div className="">
        <div className={otherBlocksList.length>0 ? styles.basicForm : styles.basicForm1}>
            <table className={`p-0 ${styles.earlyCheckInChargesBody}`}>
              <thead className={styles.otherChargesheading}>
                <th className={styles.rightBorder}>
                  <span className={styles.tableHeading}>{tabelHeader}</span>
                </th>
                <th className={""}>
                  <span className={styles.tableHeading}>{tabelHeader2}</span>
                </th>
              </thead>
              <tbody>
                {otherBlocksList.length > 0 &&
                  otherBlocksList.map((item, index, array) => (
                    <tr key={index} className={``}>
                      {console.log(item)}
                      <td className={` ${styles.rightBorder} text-left`}>
                        {item.block}
                      </td>
                      <td className={` text-left`}>
                        <InputBox
                          type="number"
                          className={`small-placeholder ${styles.styleInputBox}`}
                          ratefiled={true}
                          // disabled={preview}
                          name={`floorsPerBlock.${index}.numberOfFloors`}
                          placeholder="Enter number of floors"
                          errors={errors}
                          value={item.numberOfFloors}
                          onKeyDown={numberInputMax}
                          onChange={(event) => handleUserInput(index, event)}
                          register={register(
                            `floorsPerBlock.${index}.numberOfFloors`,
                            {
                              required:
                                item.numberOfFloors == "" ||
                                item.numberOfFloors == null
                                  ? // !saveDraft
                                    true
                                  : false,
                              pattern: /^[0-9\b]+$/,
                              validate: {
                                max100: (value) => {
                                  if (parseInt(value) > 100) {
                                    return "Maximum value is 100";
                                  }
                                  return true;
                                },
                              },
                            }
                          )}
                        />
                        <FormErrorMessage
                      className={styles.error}
                          error={
                            errors?.floorsPerBlock?.[index]?.numberOfFloors
                          }
                          messages={{
                            required: "Enter number of floors is required",
                            pattern: "Enter number of floors is invalid",
                            max100: "Maximum number of floors is 100",
                          }}
                        />
                      </td>
                    </tr>
                  ))}
              </tbody>
            </table>
        </div>
      </div>
    </div>
  );
};

export default BlockTable;
