import React, { useMemo } from "react";
import styles from "./styles.module.scss";
import InputBox from "components/UI/common/InputBox/InputBox";
import FormErrorMessage from "components/UI/common/ErrorMessage";
import { numberInputMax } from "helpers/validation";
import { style } from "@mui/system";

const FloorTable = ({
  tabelHeader,
  tabelHeader2,
  tabelHeader3,
  errors,
  register = () => { },
  otherFloorsList = [],
  otherFloor = [],
  activeTab,
  watch,
}) => {
  const flatsPerFloor = watch("flatsPerFloor") || [];
  console.log(otherFloorsList);
  const showFloorComments = useMemo(() => {
    if (flatsPerFloor?.length)
      return flatsPerFloor?.some(
        (floor) => String(floor.flats) === "0" && floor?.block === activeTab
      );
    return false;
  }, [flatsPerFloor?.some((floor) => String(floor?.flats) === "0"), activeTab]);
  console.log(otherFloorsList);
  return (
    <div className={styles.ChargesForm}>
      <div className="">
        <div
          className={
            otherFloorsList.length > 0 ? styles.basicForm : styles.basicForm1
          }
        >
          <table className={`p-0 ${styles.earlyCheckInChargesBody}`}>
            <thead className={styles.otherChargesheading}>
              <th className={styles.rightBorder}>
                <span className={styles.tableHeading}>{tabelHeader}</span>
              </th>
              <th className={showFloorComments ? styles.rightBorder : ""}>
                <span className={styles.tableHeading}>{tabelHeader2}</span>
              </th>
              {showFloorComments && (
                <th>
                  <span className={styles.tableHeading}>{tabelHeader3}</span>
                </th>
              )}
            </thead>
            <tbody>
              {otherFloorsList.length > 0 &&
                otherFloorsList.map((item, index, array) => {
                  console.log(item, "item");
                  const Shortened = watch("shortened");
                  const blockName = watch("blockName");
                  const blockNameFormat = watch("blockNameFormat");
                  console.log(item, "item");
                  console.log(blockName, Shortened, "item");
                  console.log(watch(""));
                  let isDisplay = false,
                    isEven = false;

                  if (blockNameFormat === "CUSTOM") {
                    if (Shortened && item.shortenedName === activeTab) {
                      isDisplay = true;
                    }
                    if (!Shortened && item.blockName === activeTab) {
                      isDisplay = true;
                    }
                  } else {
                    if (item.block === activeTab) {
                      isDisplay = true;
                    }
                  }

                  return (
                    <tr
                      key={index}
                      className={`${isDisplay == true ? styles.show : "d-none"
                        } ${item.floor % 2 == 0 ? styles.odd : styles.even} `}

                    >
                      <td className={` ${styles.rightBorder} ${item?.lastIndex ? styles.leftBottomRadius : ""} text-left`}>
                        {item?.floor}
                      </td>
                      <td
                        className={` ${showFloorComments ? styles.rightBorder : ""
                          } ${item?.lastIndex && !showFloorComments ? styles.rightBottomRadius : ""} text-left`}
                      >
                        <InputBox
                          type="number"
                          ratefiled={true}
                          className={`small-placeholder ${styles.styleInputBox}`}
                          name={`flatsPerFloor.${index}.flats`}
                          placeholder="Enter number of flats"
                          errors={errors}
                          onKeyDown={numberInputMax}
                          value={item?.flats}
                          register={register(`flatsPerFloor.${index}.flats`, {
                            required:
                              item?.flats == "" || item?.flats == null
                                ? // !saveDraft
                                true
                                : false,
                            pattern: /^[0-9\b]+$/,
                            validate: {
                              max100: (value) => {
                                if (parseInt(value) > 1000) {
                                  return "Maximum value is 1000";
                                }
                                return true;
                              },
                            },
                          })}
                        />

                        <FormErrorMessage
                          className={styles.error}
                          error={errors?.flatsPerFloor?.[index]?.flats}
                          messages={{
                            required: "Enter number of flats is required",
                            pattern: "Enter number of flats is invalid",
                            max100: "Maximum number of flats is 100",
                          }}
                        />
                      </td>
                      {showFloorComments && (
                        <td className={`${item?.lastIndex ? styles.rightBottomRadius : ""}`}>
                          <span
                            className={`col-5 text-left me-3 ${styles.comments}  `}
                          >
                            {String(watch(`flatsPerFloor.${index}.flats`)) ===
                              "0" ||
                              String(watch(`flatsPerFloor.${index}.flats`)) ==
                              "00" ? (
                              <p className={"mb-0"}>
                                There are no flats on the ground floor as its a
                                lobby
                              </p>
                            ) : (
                              <p className="mb-0"></p>
                            )}
                          </span>
                        </td>
                      )}
                    </tr>
                  );
                })}
            </tbody>
          </table>
        </div>
      </div>
    </div>
  );
};

export default FloorTable;
